const clamp = (num, min, max) => {
  return num <= min ? min : num >= max ? max : num;
}

const map = (num, originalMin, originalMax, targetMin, targetMax) => {
  return (num - originalMin) * (targetMax - targetMin) / (originalMax - originalMin) + targetMin;
}

const formatDate = (dateObject, lang) => {
  return Intl.DateTimeFormat(lang, {
    year: 'numeric',
    month: 'long',
  }).format(dateObject);
}

export {
  clamp,
  map,
  formatDate,
};