import { default as React } from 'react';
import { useStorage } from '../services';
import { useTranslation } from 'react-i18next';
import { Tile } from '../components';

const WorkPage = () => {
	const { getAllWork } = useStorage();
	const { t } = useTranslation();
	let smallWork = [];
	let bigWork = [];

	getAllWork().forEach((work) => {
		if (work.id === '0') {
			bigWork.push(work);
			return;
		}

		smallWork.push(work);
	});

	return (
		<div className="page page--centered workPage">
			<h1 className="page__title">{t('pages:work')}</h1>
			<div className="page__content page__content--large">
				<div className="tileContainer workPage__tileContainer">
					{getAllWork() &&
						getAllWork().map((work) => {
							return (
								<Tile
									key={work.id}
									classes="workPage__tile"
									size={bigWork.includes(work) && '2x1'}
									tileImageURL={work.media.main}
									tileName={work.name}
									id={work.id}
								/>
							);
						})}
				</div>
			</div>
		</div>
	);
};

export default WorkPage;
