import React from 'react';
import classnames from 'classnames';
import { Link } from 'react-router-dom';
import * as Routes from '../../routes';

export const Tile = ({ classes, size, tileName, tileImageURL, id }) => {
	return (
		<Link to={Routes.WORK_DETAIL.replace(':id', id)} className={classnames('tile', size && `tile--${size}`, classes)}>
			<div className="tile__title">{tileName}</div>
			<img className="tile__image" src={tileImageURL} alt={tileName}/>
		</Link>
	);
};
