import {
	default as React,
	useContext,
	createContext,
} from 'react';

import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import * as translations from '../lang/index';

const LocaleContext = createContext();
const useLocale = () => useContext(LocaleContext);

const LocaleProvider = ({ children }) => {
	const resources = {
		en: {
			pages: translations.pagesEN,
			content: translations.contentEN,
			general: translations.generalEN,
			work: translations.workEN,
		},
		nl: {
			pages: translations.pagesNL,
			content: translations.contentNL,
			general: translations.generalNL,
			work: translations.workNL,
		},
	};

	i18n
		.use(initReactI18next) // passes i18n down to react-i18next
		.init({
			resources,
			lng: 'nl',
			fallbackLng: ["nl", "en", "dev"],

			keySeparator: false, // we do not use keys in form messages.welcome

			interpolation: {
				escapeValue: false, // react already safes from xss
			},
		});

	const setLocale = (locale) => {
		i18n.changeLanguage(locale);
	};

	const exports = {
		setLocale,
		i18n,
	};

	return (
		<LocaleContext.Provider value={exports}>{children}</LocaleContext.Provider>
	);
};

export { LocaleContext, LocaleProvider, useLocale };
