import React, { useEffect, useState } from 'react';
import classnames from 'classnames';
import { FaGithub, FaLink } from 'react-icons/fa';
import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';
import * as Routes from '../../routes';
import * as Utils from '../../utilities';

export const Showcase = ({ classes, data, globalInfo }) => {
	const { t, i18n } = useTranslation();
	const [date, setDate] = useState();
	const media = data.media;
	const lang = i18n.language;

	useEffect(() => {
		if(!data || !data.date) return;
		let newDate = new Date(data.date.seconds * 1000);
		newDate = Utils.formatDate(newDate, lang);
		setDate(newDate);
	}, [data, lang]);
	
	return (
		<div className={classnames('showcase', classes)}>
			<img
				src={media && media.main}
				alt={data && data.name}
				className="showcase__image"
			/>
			<div className="showcase__index">
				{globalInfo.index + 1} / {globalInfo.total}
			</div>
			<div className="showcase__infoContainer">
				<div className="showcase__info">
					<h2 className="showcase__name">{data.name || ''}</h2>
					<h3 className="showcase__type">{data.type ? data.type[lang] : ''}</h3>
					<div className="showcase__synopsis">
						{data.synopsis ? data.synopsis[lang] : ''}
					</div>
					<div className="workDetailPage__date">{date}</div>
					{/* <p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p>
					<p className="showcase__general">
						Lorem, ipsum dolor sit amet consectetur adipisicing elit. Veniam
						nobis blanditiis, dignissimos, tempore laborum iste porro ipsa
						dolorem at nihil iure quisquam est officia quaerat ipsam fuga, non
						ipsum assumenda!
					</p> */}
				</div>
				<div className="showcase__actions">
					<div className="showcase__links">
						{data.links && (
							<>
								{data.links.github && (
									<a
										href={data.links.github}
										className="link showcase__link"
										target="_blank"
										rel="noopener noreferrer"
									>
										<FaGithub />
									</a>
								)}
								{data.links.demo && (
									<a
										href={data.links.demo}
										className="link showcase__link"
										target="_blank"
										rel="noopener noreferrer"
									>
										<FaLink />
									</a>
								)}
							</>
						)}
					</div>
					<Link to={Routes.WORK_DETAIL.replace(':id', data.id)} className="button showcase__moreInfo">{t('general:moreInfo')}</Link>
				</div>
			</div>
		</div>
	);
};
