import React, { useState } from 'react';
import classnames from 'classnames';
import { FaBars, FaTimes, FaGithub, FaLinkedin } from 'react-icons/fa';

import * as Routes from '../../routes';
import { useLocation, Link } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

export const Menu = () => {
	const [open, setOpen] = useState(false);
	const currentPath = useLocation().pathname;
	const { t } = useTranslation();

	let navs = [
		{ route: Routes.LANDING, text: t('pages:home') },
		{ route: Routes.ABOUT, text: t('pages:about') },
		{ route: Routes.WORK, text: t('pages:work') },
		{ route: Routes.CONTACT, text: t('pages:contact') },
	];

	const toggleMenu = () => {
		setOpen((prev) => !prev);
	};

	const onLinkClick = () => {
		setOpen(false);
	}

	return (
		<aside className="menu">
			<div className="menu__icon" onClick={toggleMenu}>
				{open ? <FaTimes /> : <FaBars />}
			</div>
			<div
				className={classnames(
					'menu__content',
					!open && 'menu__content--closed'
				)}
			>
				<div className="menu__navigation">
					{navs &&
						navs.map((nav) => (
							<Link
								to={nav.route}
								key={nav.route}
								className={classnames(
									'navLink',
									'menu__navLink',
									nav.route === currentPath && 'menu__navLink--selected'
								)}
								onClick={onLinkClick}
							>
								{nav.text}
							</Link>
						))}
				</div>
				<div className="menu__links">
					<a href="https://github.com/Thomva" className="link menu__link">
						<FaGithub />
					</a>
					<a
						href="https://www.linkedin.com/in/thomas-v-57b817107/"
						className="link menu__link"
					>
						<FaLinkedin />
					</a>
				</div>
			</div>
		</aside>
	);
};
