import React, { useState, useEffect } from 'react';
import classnames from 'classnames';
import { useRef } from 'react';

export const SwitchToggle = ({ className, labelOff, labelOn, id, onChange, defaultValue = true }) => {
  const [ state, setState ] = useState(defaultValue);
  const [ handleStyle, setHandleStyle ] = useState({});
  const labelOnRef = useRef();
  const labelOffRef = useRef();

  useEffect(() => {
    if (!labelOnRef) return;
    if (!labelOffRef) return;
    setHandleStyle(state ?
      {
        width: `${labelOnRef.current.offsetWidth}px`,
        marginLeft: `${labelOffRef.current.offsetWidth}px`
      }
      : {
        width: `${labelOffRef.current.offsetWidth}px`,
        marginLeft: `0`
      });
  }, [labelOnRef, labelOffRef, state]);

  const onClick = (e) => {
    setState(!state);
  }

  useEffect(() => {
    if (onChange) onChange(state);
  }, [state, onChange]);

	return (
		<div className={classnames('input input--switch switch', className)}>
      <input type="hidden" name={id} id={id} value={state}/>
      <div className="switch__container" onClick={onClick}>
        {labelOnRef && labelOffRef && <div className="switch__handle" style={handleStyle}></div>}
        <div className={classnames('switch__label', !state && 'switch__label--active')} ref={labelOffRef}>{labelOff}</div>
        <div className={classnames('switch__label', state && 'switch__label--active')} ref={labelOnRef}>{labelOn}</div>
      </div>
		</div>
	);
};
