import React from 'react';
import './app.scss';

import { HashRouter as Router, Redirect, Switch } from 'react-router-dom';

import * as Routes from './routes';
import { HomePage, AboutPage, WorkPage, ContactPage, WorkDetailPage } from './pages';
import { LocaleProvider, WorkProvider, StorageProvider } from './services';
import { RouteWithLayout } from './utilities';
import { BaseLayout } from './layouts';

function App() {
	return (
		<div className="app overlayTexture">
			<StorageProvider>
				<LocaleProvider>
					<WorkProvider>
						<Router basename={'/'}>
							<Switch>
								<RouteWithLayout
									exact
									path={Routes.LANDING}
									component={HomePage}
									layout={BaseLayout}
								/>
								<Redirect from={Routes.HOME} to={Routes.LANDING} />
								<RouteWithLayout
									exact
									path={Routes.ABOUT}
									component={AboutPage}
									layout={BaseLayout}
								/>
								<RouteWithLayout
									exact
									path={Routes.WORK}
									component={WorkPage}
									layout={BaseLayout}
								/>
								<RouteWithLayout
									exact
									path={Routes.CONTACT}
									component={ContactPage}
									layout={BaseLayout}
								/>
								<RouteWithLayout
									exact
									path={Routes.WORK_DETAIL}
									component={WorkDetailPage}
									layout={BaseLayout}
								/>
							</Switch>
						</Router>
					</WorkProvider>
				</LocaleProvider>
			</StorageProvider>
		</div>
	);
}

export default App;
