import {
	default as React,
	useContext,
	createContext,
	useEffect,
	useState,
} from 'react';

import { Firestore } from '../storage';

const StorageContext = createContext();
const useStorage = () => useContext(StorageContext);

const StorageProvider = ({ children }) => {
	const [allWork, setAllWork] = useState([]);

	useEffect(() => {
		(async () => {
		const db = new Firestore();
		const fetchedWork = await db.getCollection('work');
			setAllWork(fetchedWork);
		})();
	}, []);

	const getAllWork = () => {
		return allWork;
	};

	const getWorkById = (id) => {
		return allWork.find((work) => work.id === id);
	};

	const exports = {
		getAllWork,
		getWorkById,
	};

	return (
		<StorageContext.Provider value={exports}>
			{children}
		</StorageContext.Provider>
	);
};

export { StorageContext, StorageProvider, useStorage };
