import { default as React, useCallback, useEffect, useState } from 'react';
import { useParams } from 'react-router-dom';
import { useStorage } from '../services';
import { useTranslation } from 'react-i18next';
import { FaGithub, FaLink } from 'react-icons/fa';
import * as Utils from '../utilities';

const WorkDetailPage = () => {
	const [data, setData] = useState();
	const { getWorkById } = useStorage();
	const { id } = useParams();
	const { t, i18n } = useTranslation();
	const lang = i18n.language;
	const [date, setDate] = useState();

	const getData = useCallback(() => {
		if (!id) return;
		(async () => {
			const fetchedData = await getWorkById(id);
			setData(fetchedData);
		})();
	}, [id, getWorkById]);

	useEffect(() => {
		getData();
	}, [getData]);

	useEffect(() => {
		if(!data || !data.date) return;
		let newDate = new Date(data.date.seconds * 1000);
		newDate = Utils.formatDate(newDate, lang);
		setDate(newDate);
	}, [data, lang]);

	return !data ? (
		<></>
	) : (
		<div className="page page--centered workDetailPage">
			<h1 className="page__title workDetailPage__title">{data.name}</h1>
			<h3 className="workDetailPage__type">{data.type && data.type[lang]}</h3>
			<div className="workDetailPage__tagContainer">
				{data.tags &&
					data.tags.map((tag) => (
						<div key={tag} className="workDetailPage__tag">
							{tag}
						</div>
					))}
			</div>
			<div className="workDetailPage__intro">
				<img
					src={data.media && data.media.main}
					alt={data.name}
					className="workDetailPage__image"
				/>
				<div className="workDetailPage__descriptionContainer">
					<div className="workDetailPage__introText">
						<p
							className="workDetailPage__description"
							dangerouslySetInnerHTML={{
								__html: data.description && data.description[lang],
							}}
						></p>
						<div className="workDetailPage__date">{date}</div>
					</div>
					<div className="workDetailPage__links">
						{data.links && (
							<>
								{data.links.github && (
									<a
										href={data.links.github}
										className="link showcase__link"
										target="_blank"
										rel="noopener noreferrer"
									>
										<FaGithub />
									</a>
								)}
								{data.links.demo && (
									<a
										href={data.links.demo}
										className="link showcase__link"
										target="_blank"
										rel="noopener noreferrer"
									>
										<FaLink />
									</a>
								)}
							</>
						)}
					</div>
				</div>
			</div>
			<div className="page__content workDetailPage__content">
				{data.assignment && (
					<div className="workDetailPage__dataElement">
						<h3 className="workDetailPage__dataTitle">
							{t('work:assignment')}
						</h3>
						<p
							className="workDetailPage__dataContent"
							dangerouslySetInnerHTML={{ __html: data.assignment[lang] }}
						></p>
					</div>
				)}

				{data.client && (
					<div className="workDetailPage__dataElement">
						<h3 className="workDetailPage__dataTitle">{t('work:client')}</h3>
						{data.client[lang]}
					</div>
				)}

				{data.learnings && (
					<div className="workDetailPage__dataElement">
						<h3 className="workDetailPage__dataTitle">{t('work:learnings')}</h3>
						{data.learnings[lang]}
					</div>
				)}

				{data.media && data.media.all && (
					<div className="workDetailPage__mediaContainer">
						{data.media.all.map(
							(media) =>
								media.src && (
									<div key={media.src} className="workDetailPage__mediaElement">
										<img src={media.src} alt={media.title} />
									</div>
								)
						)}
					</div>
				)}
			</div>
		</div>
	);
};

export default WorkDetailPage;
