import React, { useState, useEffect, useRef, useCallback } from 'react';
import classnames from 'classnames';
import * as Utils from '../../utilities';

export const HorizontalScroll = ({
	children,
	classes,
	scrollSpeed = 1,
	onWheel,
}) => {
	const contentRef = useRef(null);
	const containerRef = useRef(null);
	const [contentPosLeft, setContentPosLeft] = useState(0);
	const [contentStyle, setContentStyle] = useState({
		position: 'absolute',
	});
	const [max, setMax] = useState('1000px');

	const setWidths = useCallback(() => {
		containerRef.current.style.width = `${
			contentRef.current.getBoundingClientRect().width
		}px`;
		setMax(containerRef.current.getBoundingClientRect().width);
	}, [contentRef, containerRef]);

	useEffect(() => {
		window.addEventListener('resize', setWidths);

		return () => window.removeEventListener('resize', setWidths);
	}, [setWidths]);

	useEffect(() => {
		setContentStyle((prev) => ({
			...prev,
			left: -contentPosLeft,
		}));
		if (onWheel)
			onWheel({
				position: contentPosLeft,
				min: 0,
				max,
			});
	}, [contentPosLeft, max, onWheel]);

	useEffect(() => {
		setWidths();
	}, [contentRef, containerRef, setWidths]);

	const onScroll = useCallback((event = {deltaY: 0}) => {
		const deltaY = scrollSpeed * event.deltaY;
		setContentPosLeft((prev) => Utils.clamp(prev + deltaY, 0, max));
	}, [max, scrollSpeed]);

	useEffect(() => {
		onScroll();
	}, [max, onScroll]);

	const onContentLoad = () => {
		setWidths();
	};

	return (
		<div
			className={classnames('horizontalScroll', classes)}
			onWheel={onScroll}
			ref={containerRef}
		>
			<div
				className="horizontalScroll__content"
				ref={contentRef}
				style={contentStyle}
				onLoad={onContentLoad}
			>
				{children}
			</div>
		</div>
	);
};
