import { default as React, useEffect, useRef, useCallback } from 'react';
import { useState } from 'react';
import { Typer } from 'react-typetool';
import { useTranslation } from 'react-i18next';
import classnames from 'classnames';

import { HorizontalScroll, ShowcaseGroup } from '../components';
import * as Utils from '../utilities/Utils';

const HomePage = () => {
	const [shouldBlink, setShouldBlink] = useState(true);
	const [finishedTyping, setFinishedTyping] = useState(false);
	const [shouldShowCaret, setShouldShowCaret] = useState(true);
	const [windowSize, setWindowSize] = useState({
		height: window.innerHeight,
		width: window.innerWidth,
	});
	const { t } = useTranslation();
	const [showIntroParagraph, setShowIntroParagraph] = useState(false);
	const [caretAnimationDone, setCaretAnimationDone] = useState(false);
	let typerCaretRef = useRef(null);
	let scrollTrackRef = useRef(null);
	let scrollTrackFillRef = useRef(null);

	const updateWindowSize = () => {
		setWindowSize({
			height: window.innerHeight,
			width: window.innerWidth,
		});
	};

	useEffect(() => {
		const resizeListener = window.addEventListener('resize', () => {
			updateWindowSize();
		});

		return () => window.removeEventListener('resize', resizeListener);
	}, []);

	const positionCaretVertical = useCallback(() => {
		const caret = typerCaretRef.current;
		const positionTop = windowSize.height / 2 - caret.offsetHeight / 2;
		caret.style.top = `${positionTop}px`;
	}, [typerCaretRef, windowSize]);

	const positionCaret = useCallback(() => {
		const caret = typerCaretRef.current;

		const positionTimeout = setTimeout(() => {
			positionCaretVertical();
			caret.style.right = '100px';
			caret.style.width = '100px';
		}, 10);

		const waitForFinishTimeout = setTimeout(() => {
			setCaretAnimationDone(true);
		}, 1000);

		return () => {
			clearTimeout(positionTimeout);
			clearTimeout(waitForFinishTimeout);
		};
	}, [typerCaretRef, positionCaretVertical]);

	const stopTyperCaret = useCallback(() => {
		setShouldBlink(false);
		const caret = typerCaretRef.current;
		const currentCaretPosition = caret.getBoundingClientRect();
		const positionRight = windowSize.width - currentCaretPosition.left - 9.6;
		caret.style.position = 'fixed';
		caret.style.right = `${positionRight}px`;
		caret.style.top = `${currentCaretPosition.top}px`;
		positionCaret();
	}, [typerCaretRef, windowSize, positionCaret]);

	const onFinishTyping = useCallback(() => {
		if (finishedTyping) return;
		if (!typerCaretRef) return;
		stopTyperCaret();
		setShouldShowCaret(true);
		setShowIntroParagraph(true);
		setFinishedTyping(true);
	}, [typerCaretRef, stopTyperCaret, finishedTyping]);

	const updateScrollTrackFill = (scrollData) => {
		scrollTrackRef.current.style.width = 'calc(100% - (100vw - 150px) + 50vw)';
		const trackWidth = scrollTrackRef.current.offsetWidth;
		const caret = typerCaretRef.current;
		const trackPos = windowSize.width / 2 - 150;
		const customPosition =
			scrollData.position - trackPos + caret.offsetWidth / 2;
		let fillWidth = Utils.map(
			customPosition,
			scrollData.min,
			scrollData.max - windowSize.width / 2 + 150,
			0,
			trackWidth
		);
		fillWidth -= caret.offsetWidth / 2;
		fillWidth = Utils.clamp(fillWidth, 0, trackWidth);
		scrollTrackFillRef.current.style.width = `${fillWidth}px`;
	};

	const onWheel = (scrollData) => {
		const hasScrolled = scrollData.position > 0;
		if (hasScrolled && !finishedTyping) onFinishTyping();
		const caret = typerCaretRef.current;
		const maxCaretPosRight = windowSize.width / 2 - caret.offsetWidth / 2;
		const caretPosRight = Utils.clamp(
			scrollData.position + 100,
			100,
			maxCaretPosRight
		);
		if (caretAnimationDone || hasScrolled) {
			caret.style.transition = 'right 200ms ease-out';
			caret.style.right = `${caretPosRight}px`;
		}
		updateScrollTrackFill(scrollData);
	};

	useEffect(() => {
		positionCaretVertical();
	}, [positionCaretVertical, windowSize]);

	return (
		<div className="page homePage">
			<HorizontalScroll onWheel={onWheel}>
				<div className="homePage__introContainer">
					<div className="homePage__introText">
						<Typer
							text={t('content:introTyper')}
							classes="introTyper"
							blink={shouldBlink}
							onFinish={onFinishTyping}
							showCaret={shouldShowCaret}
							getCaretRef={(ref) => (typerCaretRef = ref)}
							useDefaultStyle={false}
						/>
						<p
							className={classnames(
								'homePage__introParagraph',
								!showIntroParagraph && 'homePage__introParagraph--hide'
							)}
							dangerouslySetInnerHTML={{ __html: t('content:introParagraph') }}
						></p>
					</div>
				</div>
				<ShowcaseGroup />
				<div className="scrollTrack" ref={scrollTrackRef}>
					<div className="scrollTrack__fill" ref={scrollTrackFillRef}></div>
				</div>
			</HorizontalScroll>
		</div>
	);
};

export default HomePage;
